.paginatorButton{
    height: 20px;
    width: 60px;
    margin: 0 5px 0 5px;
    font-weight: 500;
    font-size: 10px;
    font-family: Arial;
    color: black;
    text-decoration: none;
    border-radius: 3px;
    background: #ff9133;
    box-shadow: 0 -3px #f57100 inset;
    transition: 0.2s;
    cursor: pointer;
}
.paginatorButton:hover { background: #f57100; }
.paginatorButton:active {
    background: #eb9347;
    box-shadow: 0 3px #dd8c00 inset;
}


.selectedPage {
    font-weight: bold;
}
.unselectedPage {
    cursor: pointer;
}